.recent-posts{

        display: grid;
        align-items: center;
        justify-items: center;

    .recent-posts_wrapper{
            display: grid;
            grid-row-gap: 21px;
            width: 100%;

    .recent-posts_heading{
        color: $color-blue-one;
        font-size: 14px;
    }

    .recent-posts_posts{
        display: grid;
        justify-content: space-evenly;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 42px;
    }
    
}}